import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IOptionsPsOptionCashAndChequeExposed } from 'src/app/commonBussinessSRC/psComponents/ps-container-lookup-option/ps-option-cash-and-cheque/ps-option-cash-and-cheque.component.interface';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';

@Component({
  selector: 'ps-option-cash-and-cheque',
  templateUrl: './ps-option-cash-and-cheque.component.html',
  styleUrls: ['./ps-option-cash-and-cheque.component.scss'],
})
export class PsOptionCashAndChequeComponent extends PsTemplateListPage implements OnInit {
  @Output() reloadFct = new EventEmitter<string>();
  @Input() options: IOptionsPsOptionCashAndChequeExposed;
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  private showSingleAccount = false;
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();

  @Output() public onClickCard = new EventEmitter<any>();

  constructor() {
    super();
  }

  ngOnInit() {
    super.init();
    this.populateHeaderValueMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
  }
  getContainerLookUpOptions(val) {
    return this.prepareLookUp(val);
  }

  private prepareLookUp(card: any): any {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.currencyFlag = 'currencyBriefNameEnglish';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.balanceMapping = this.balanceMapping();
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.translateSubTitle = this.options.translate;
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.pageData = {
      operID: CommonBussinessConstant.CHEQUEBOOK_REPORT_OPER,
      title: 'cash_and_cheque_collection_request_report_key'
    };
    lookupDetails.actionDetailsOptions = [
      {
        id: 'details',
        label: 'details_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'document',
            labelOptions: {
              labelKey: 'details_key'
            }
          },
          route: card.operId === CommonBussinessConstant.CASH_AND_CHEQUE_REQ_OPER_ID ? 'cash-and-cheque-collection' : 'bulk-cash-request',
          pageOptions: {
            operId: card.operId,
            iconName: 'edit1',
            title: card.operId === CommonBussinessConstant.CASH_AND_CHEQUE_REQ_OPER_ID ? 'cash_and_cheque_collection_key' : 'bulk_cash_request_key'
          },
          navigationOptions: {
            queryParams: { readOnlypage: true },
          },
        }
      },
      ((card.statusCode !== 'CL' && card.statusCode !== 'C') ?
        {
          id: 'edit',
          label: 'edit_key',
          actionHyperlink: {
            group: this.options.group,
            iconOptions: {
              iconName: 'edit1',
              labelOptions: {
                labelKey: 'edit_key'
              }
            },
            route: card.operId === CommonBussinessConstant.CASH_AND_CHEQUE_REQ_OPER_ID ? 'cash-and-cheque-collection' : 'bulk-cash-request',
            pageOptions: {
              operId: card.operId,
              iconName: 'edit1',
              title: card.operId === CommonBussinessConstant.CASH_AND_CHEQUE_REQ_OPER_ID ? 'cash_and_cheque_collection_key' : 'bulk_cash_request_key'
            },
            preCallFunction: {
              func() {
                return new Promise<any>((resolve, reject) => {
                  resolve(this.executionClass.prepareData(lookupDetails.actionDetailsOptions[0].actionHyperlink));
                });
              },
              executionClass: this,
              params: []
            },
            navigationOptions: {
              queryParams: { readOnlypage: false },
            },
          }
        }
        : {})
      , ((card.statusCode === 'N' || card.statusCode === 'TD') ?
        {
          id: 'cancel',
          labelOptions: {
            labelKey: 'cancel_key'
          },
          buttonIcon: 'close',
          actionUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
          cssClass: 'fab-edit-button',
          label: 'cancel_key',
          iconName: 'close',
          actionType: 'cancel',
          operId: card.operId,
          filterParamValues: [],
          reloadList: true
        } : {})
    ];
    
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }
    return lookupDetails;
  }

  reloadList() {
    this.reloadFct.emit();
  }

  private populateHeaderValueMap() {
    let requestTypeMap: IMapKeyValue = {};
    let depositeCashMap: IMapKeyValue = {};
    let depositeChequeMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    let headerSubtitleMap1: IMapKeyValue = {};
    let collectionLocMap: IMapKeyValue = {};
    let chqSerialNoMap: IMapKeyValue = {};
    let modeOfCollectionMap: IMapKeyValue = {};

    requestTypeMap = {
      key: 'requestTypeDesc',
      value: '',
      isEdit: false
    };

    depositeCashMap = {
      key: 'depositCash',
      value: ''
    };

    depositeChequeMap = {
      key: 'depositCheque',
      value: ''
    };

    collectionLocMap = {
      key: 'collectionLocationDesc',
      value: ''
    };

    modeOfCollectionMap = {
      key: 'collectionModeDesc',
      value: ''
    };

    chqSerialNoMap = {
      key: 'chequeSerialNo',
      value: ''
    };

    headerTitleMap = {
      key: 'title',
      value: 'fromAccount',
      isEdit: false
    };

    headerSubtitleMap = {
      key: 'subTitle',
      value: this.options.isCash ? 'depositorName' : 'chequeBankName',
      isEdit: false,
      showMappingOnNoValue: false
    };


    headerSubtitleMap1 = {
      key: 'subTitle',
      value: 'currencyDescription',
      isEdit: false
    };

    if (this.options.operation === 'cashandchequereq') {
     //this.labelValuesMap.set('request_type_key', requestTypeMap);
      if (this.options.isCash) {
        this.labelValuesMap.set('', depositeCashMap);
      } else {
        this.labelValuesMap.set('', depositeChequeMap);
        this.labelValuesMap.set('cheque_serial_no_key', chqSerialNoMap);
      }
      this.labelValuesMap.set('collection_location_key', collectionLocMap);


      // will be used as header to template card in look up details component
      this.headerMap.set('title', headerTitleMap);
      this.headerMap.set('subTitle', headerSubtitleMap);
    } else {
      this.labelValuesMap.set('mode_of_collection_key', modeOfCollectionMap);
      this.headerMap.set('title', headerTitleMap);
      this.headerMap.set('subTitle', headerSubtitleMap1);
    }

  }

  private balanceMapping(): any[] {
    const balanceMappingList = [
      (this.options.isCash ? {
        key: 'deposite_amount_key',
        value: 'transactionAmount'
      } : {
        key: 'amount_key',
        value: 'transactionAmount'
      }),
      {
        key: 'currency_key',
        value: 'currencyDescription'
      }
    ];

    return balanceMappingList;
  }

  updateCard(card: any) {
    if (card && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === card);
      this.options.item = this.options.itemList[0];
      this.showSingleAccount = !this.showSingleAccount;
      this.options.showItemPopUp = !this.options.showItemPopUp;
      // this.options.showSelectedCard = false;
    }
    this.onClickCard.emit(this.options);
  }

  prepareData(data) {

  }


}
